/************************************************

Stylesheet: Global Stylesheet

*************************************************/

html {
	scroll-behavior: smooth;
}

/*********************
POSTS & CONTENT STYLES
*********************/
.page-title {
	// Remove Foundation vcard styling from author page titles
	.vcard {
		border: 0px;
		padding: 0px;
	}
}

// Post meta
.byline {
	color: #999;
}

// Entry content
.entry-content {
	img {
		max-width: 100%;
		height: auto;
	}

	.alignleft,
	img.alignleft {
		margin-right: 1.5em;
		display: inline;
		float: left;
	}
	.alignright,
	img.alignright {
		margin-left: 1.5em;
		display: inline;
		float: right;
	}
	.aligncenter,
	img.aligncenter {
		margin-right: auto;
		margin-left: auto;
		display: block;
		clear: both;
	}

	video,
	object {
		max-width: 100%;
		height: auto;
	}

	pre {
		background: #eee;
		border: 1px solid #cecece;
		padding: 10px;
	}
}

.wp-caption {
	max-width: 100%;
	background: #eee;
	padding: 5px;

	// Images inside wp-caption
	img {
		max-width: 100%;
		margin-bottom: 0;
		width: 100%;
	}

	p.wp-caption-text {
		font-size: 0.85em;
		margin: 4px 0 7px;
		text-align: center;
	}
}

.gallery dl {
	width: 100%;
	float: left;
	padding-left: 0.9375rem;
	padding-right: 0.9375rem;
}
.gallery dl:last-child:not(:first-child) {
	float: right;
}

