// Lists
// ------------------------------------------ /
.kw-u-clean-list {
    margin: 0; padding: 0;
    list-style: none !important;
}


.kw-u-faded {
    opacity: $faded;
}



// Display
// ------------------------------------------ /
.kw-u-display-block { display: block !important; }
.kw-u-display-none { display: none !important; }
.kw-u-display-inline { display: inline !important; }
.kw-u-display-inline-block { display: inline-block !important; }

// Alignment **note these are wordpress classes**
// ------------------------------------------ /
.aligncenter{
    margin: 0 auto;
    display: block;
}

// Full height and width
// ------------------------------------------ /
.kw-u-height-full {
    height: 100%;
}
.kw-u-width-full {
    width: 100%;
}

// Flexbox
// ------------------------------------------ /
.kw-u-align-self-end {
    align-self: flex-end;
}

.kw-u-align-self-start {
    align-self: flex-start
}

.kw-u-flex-grow-1 {
    flex-grow: 1;
}

// Effects
// ------------------------------------------ /
.kw-u-transition {
    transition: all $duration $easing;
}
.kw-u-transition-short {
    transition: all $duration-short $easing;
}
.kw-u-transition-long {
    transition: all $duration-long $easing;
}


$play-icon-unit : 1rem;

// Play Icon
// ------------------------------------------ /
.kw-u-icon-play {
    display: inline-block;
    position: relative;
    background-color: white;
    border-radius: 50%;
    width: $play-icon-unit*3;
    height: $play-icon-unit*3;
    box-sizing: content-box;
    z-index: 2;

    &:before{
        @extend .kw-u-transition;
        content: '';
        position: absolute;
        left: 0; top: 0; right: 0; bottom: 0;
        display: block;
        background: rgba( get-color(white) ,.3);
        border-radius: 50%;
    }

    &:after{

        // content: url('../../../images/src/play.svg');
        position: absolute;
        display: block;
        width: 40%;
        top: 50%;
        left: 50%;
        transform: translate(-40%,-36%);
    }



    &:hover,
    &:focus{

        &:before{
            //left: -6px; top: -6px; right: -6px; bottom: -6px;
            margin: -1 * ($play-icon-unit/2);
        }
    }


}


// circle Icon CTA
// ------------------------------------------ /

@mixin circle-icon-cta(){
    @content;
    position: relative;
    box-sizing: border-box;

    & > * {
        z-index: 5;
    }

    &:before{
        @extend .kw-u-transition;
        content: '';
        position: absolute;
        left: 0; top: 0; right: 0; bottom: 0;
        display: block;
        background: rgba(255,255,255,.3);
        border-radius: 50%;
    }

    &:hover,
    &:focus{

        &:before{
            //left: -6px; top: -6px; right: -6px; bottom: -6px;
            margin: -1 * ($play-icon-unit/2);
        }
    }


}


// Box Shadow
// ------------------------------------------ /

.kw-u-box-shadow{
    box-shadow: $focus-box-shadow-lg;
}



// Pseudo Mixin
// ------------------------------------------ /
@mixin pseudo($display: block, $pos: absolute, $content: ''){
    content: $content;
    display: $display;
    position: $pos;
}


// Arrow Mixin
// ------------------------------------------ /
@mixin css-arrow($color:$white, $direction:'up', $size: 6px, $position: absolute, $round: false){
    @include pseudo($pos: $position);
    width: 0;
    height: 0;
    @if $round {
        border-radius: $global-radius;
    }
    @if $direction == down {
        border-left: $size solid transparent;
        border-right: $size solid transparent;
        border-top: $size solid $color;
        margin-top: 0 - round( $size / 2.5 );
    } @else if $direction == up {
        border-left: $size solid transparent;
        border-right: $size solid transparent;
        border-bottom: $size solid $color;
        margin-bottom: 0 - round( $size / 2.5 );
    } @else if $direction == right {
        border-top: $size solid transparent;
        border-bottom: $size solid transparent;
        border-left: $size solid $color;
        margin-right: -$size;
    } @else if  $direction == left {
        border-top: $size solid transparent;
        border-bottom: $size solid transparent;
        border-right: $size solid $color;
        margin-left: -$size;
    }
}


// Create font color classes
// ------------------------------------------ /
@each $name, $color in $foundation-palette {

    .kw-u-font-color--#{$name} {

        &:not(a),
        .h1,
        .h2,
        .h3,
        .h4,
        .h5,
        .h6,
        h1,
        h2,
        h3,
        h4,
        h5,
        h6
        {
            color: get-color($name);
        }

    }

}

@mixin set-header-colors( $color ){
    @content;
    & *:not(a,  .button, label),
    & .h1,
    & .h2,
    & .h3,
    & .h4,
    & .h5,
    & .h6,
    & h1,
    & h2,
    & h3,
    & h4,
    & h5,
    & h6
    {
        color: get-color( $color ) !important;
    }
}


// Hero Background Colors map
// ------------------------------------------ /
@each $name, $color in $foundation-palette {

    .kw-c-hero--bg-#{$name}{
        background-color: get-color($name);
    }

}


.kw-u-jump-down{
    display: block;
    text-align: center;
}

// Positioning
// ------------------------------------------ /
@mixin position($position, $args) {
    @each $o in top right bottom left {
        $i: index($args, $o);
        @if $i and $i + 1 <= length($args) and type-of(nth($args, $i + 1)) == number {
            #{$o}: nth($args, $i + 1);
        }
    }
    position: $position;
}

// Positioning helpers
@mixin absolute($args: '') {
    @include position(absolute, $args);
}

@mixin fixed($args: '') {
    @include position(fixed, $args);
}

@mixin relative($args: '') {
    @include position(relative, $args);
}

//Absolute positioning centering
@mixin abs-centerer($horizontal: true, $vertical: true) {
    position: absolute;
    @if ($horizontal and $vertical) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    } @else if ($horizontal) {
        left: 50%;
        transform: translate(-50%, 0);
    } @else if ($vertical) {
        top: 50%;
        transform: translate(0, -50%);
    }
}

@mixin shadow-gradient(){

    @content;
    position: relative;
    width: 100%;

    & > * {
        position: relative;
        z-index: 3;
    }

    &:before{
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        background: linear-gradient(rgba(0,0,0, .2 ), rgba( get-color( primary ), .6 )  ,rgba(0,0,0,.7 ) );
        z-index: 2;
        top: 0;
    }

}

.kw-u-bg-overlay{
    @include shadow-gradient();
}

// divider line
// ------------------------------------------ /
@mixin line-divider( $color : 'primary', $width : 100%  ){
    background-color: get-color($color);
    width: $width;
    display: inline-block;
    height: 1px;
    margin: $spacer/3 0;
}

.kw-u-divider{
    @include line-divider();

}

// Clearfix
// ------------------------------------- //
@mixin clearfix {
    &:before,
    &:after {
        content: "";
        display: table;
    }
    &:after {
        clear: both;
    }
}

.kw-u-clearfix {
    @include clearfix;
}